import { createStore } from 'vuex'

const store = createStore({
    state() {
        return {
            room_id: "",
            location: "location",
            organization: "organiztion",
            device: {
                id: null,
                name: "device_name"
            },
            urls: [
                'http://127.0.0.1:5000/api/v1/device_settings',
                'http://192.168.3.249/slider_reg'
            ],
            last_ticket: {
                num: 0,
                prefix: "",
                time: "",
                service: {
                    id: 0,
                    name: "service"
                }
            },
            notifiations: [],
            stat: {}
        }
    },
    mutations: {
        set_room_id(state, id) {
            state.room_id = id;
        },
        set_location(state, loction) {
            state.location = loction
        },
        set_organization(state, organization) {
            state.organization = organization;
        },
        set_device(state, device) {
            state.device = device;
        },
        set_last_ticket(state, ticket) {
            state.last_ticket = ticket
        },
        add_notification(state, ticket) {
            state.notifiations.push(ticket)
        },
        del_notification(state) {
            state.notifiations.splice(0, 1)
        },
        set_state(state, stat) {
            state.stat = stat;
        }
    },
    getters: {
        get_room_id(state) {
            return state.room_id
        },
        get_url(state) {
            return state.urls
        },
        get_location(state) {
            return state.location
        },
        get_organization(state) {
            return state.organization
        },
        get_device(state) {
            return state.device
        },
        get_last_ticket(state) {
            return state.last_ticket
        },
        get_notification(state) {
            return state.notifiations
        },
        get_first_notification(state) {
            if (state.notifiations.length > 0) {
                return state.notifiations[0]
            } else {
                return {}
            }
        },
        get_state(state) {
            return state.stat
        }
    }
})

export default store