<template>
  <aside>
    <h1 v-on:click="settings = !settings; get_voices();">{{$store.getters.get_organization}}</h1>
    <div class="settings" v-if="settings">
      <select class="input" v-model="voic">
        <option v-for="item in voices" :key="item" :value="item">{{item.voiceURI}}</option>
      </select>
      <button class="input" v-on:click="voic = null">убрать голос</button>
    </div>    
    <input type="checkbox" id="title" hidden>
    <treatments></treatments>
  </aside>
  <main>
    <h1>{{$store.getters.get_location}}</h1>
    <iframe :src="$store.getters.get_url[1]" frameBorder="0"></iframe>
  </main>
  <modalwindow v-if="modal" 
  :ticket="$store.getters.get_first_notification.ticket"
  :operator="$store.getters.get_first_notification.operator"></modalwindow>
</template>

<script>
import axios from 'axios'
import modalwindow from './components/modalwindow.vue'
import treatments from './components/treatrments.vue'
export default {
  name: 'App',
  components: {
    modalwindow,
    treatments
  },
  data() {
    return {
      modal: false,
      settings: false,
      voices: [],
      voic: null,
      utterance: null,
    }
  },
  created() {
    setTimeout(() => {
      this.init();
    }, 500)
  },
  methods: {
    init() {
      axios.get(this.$store.getters.get_url[0]).then(resp => {
        this.$store.commit('set_location', resp.data.location);
        this.$store.commit('set_device', resp.data.device);
        this.$store.commit('set_organization', resp.data.organization);
        console.log(resp.data)
      });
    },
    show_modal() {
      document.querySelector('#title').click();
      if (this.$store.getters.get_notification.length > 0 && this.modal == false) {
        this.modal = true;
        if (this.voic != null){
          this.voice();
        }
        else {
            setTimeout(() => {
            this.modal = false;
            this.$store.commit('del_notification');
            setTimeout(() => {
              this.show_modal();
            }, 500)
          }, 3000)
        }
      }
    },
    voice() {
      var ticket_string = this.$store.getters.get_first_notification.ticket?.prefix + this.$store.getters.get_first_notification.ticket?.num;
      this.utterance = new SpeechSynthesisUtterance(this.$store.getters.get_first_notification.operator?.duber.replace('@', ticket_string));
      this.utterance.addEventListener('end', () => {
        this.$store.commit('del_notification');
        this.modal = false;
        setTimeout(() => {
          this.show_modal();
        }, 500)
      })
      this.utterance.voice = this.voic;
      speechSynthesis.speak(this.utterance);
    },
    cli() {
      console.log('click');
    },
    get_voices() {
      this.voices = speechSynthesis.getVoices();
    }
  },
  sockets: {
    call_client(data) {
      this.$store.commit('add_notification',  data);
      this.show_modal();
    },
    state(data) {
      this.$store.commit('set_state',  data);
    }
  }
}
</script>

